.counterContainer {
  display: flex;
  justify-content: space-evenly;
  margin: 1em auto;
  max-width: 100%;
  width: 100px;
  height: 30px;
}

.btn {
  font-size: clamp(1em, 2.5vw, 1em);
  font-weight: 500;
  background-color: #ccc;
  color: rgb(54, 54, 54);
  width: 2em;
  height: 2em;
  padding: 0;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  opacity: 0.6;
  transition: opacity, color 0.2s linear;
}

.btn:hover {
  opacity: 1;
  color: #fff;
  transition: opacity 0.2s linear;
  opacity: 1;
}
