.pagesWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 3em;
  color: rgb(207, 207, 207);
  line-height: 1.5em;
  font-size: calc((100vw - 30em) / (80 - 30) * (1.5 - 1) + 1em);
}

a {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1em 0;
}
